.gpt3__features-container__feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.gpt3__features-container__feature-title{
    flex: 1;
    max-width: 250px;
    margin-right: 2rem;

}
.gpt3__features-container__feature-title h1{
font-family: 'Manrope';
font-style: normal;
font-weight: 800;
font-size: 18;
line-height: 50px;

/* identical to box height, or 312% */

color: #FFFFFF;


}
.gpt3__features-container__feature-title div{
width: 38px;
height: 3px;
background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
margin-bottom: 0.75rem ;
}
.gpt3__features-container_feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}
.gpt3__features-container_feature-text p{
    font-family: 'Manrope';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 30px;

/* or 188% */

color: #81AFDD;
}
@media screen and (max-width:550px){
.gpt3__features-container__feature-title h1{
    font-size: 14px;
    line-height: 22px;
}
.gpt3__features-container_feature-text p{
    font-size: 12px;
    line-height: 20px;
}
.gpt3__features-container__feature{
    margin: 1rem 0;;
}
    
}